//import React, { Component } from "react";

exports.uploadData = (object, callback) => {
  const imgUrl =
    "https://images.vexels.com/media/users/3/147102/isolated/preview/082213cb0f9eabb7e6715f59ef7d322a-instagram-profile-icon-by-vexels.png";

  if (!object.name) {
    return callback(1, "Name is empty");
  }
  if (!object.profession) {
    return callback(1, "Profession is empty");
  }
  if (!object.story) {
    return callback(1, "Story is empty");
  }
  if (object.story) {
    if (object.story.split(" ").length < 10) {
      return callback(1, "Your story must contain at least 10 words.");
    }
  }
  if (!object.photo) {
    object.photo = imgUrl;
  }
  fetch("https://jsonbox.io/box_bdbe89393980ec2873d8", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(object),
  })
    .then((res) => res.json())
    .then((data) => {
      if (data.message) {
        console.log(data);
        callback(1, "Sorry we are not accepting any more stories");
      } else {
        console.log(data);
        callback();
      }
    })

    .catch((err) => {
      callback(1, "Error");
      console.log(err);
    });

  console.log(object);

  return callback();
};
