import React, { useState, useEffect } from "react";
import Base from "./Base";
import { uploadData } from "./uploadData";
import ReCAPTCHA from "react-google-recaptcha";
const App = () => {
  const [values, setValues] = useState({
    name: "",
    profession: "",
    defaultImage:
      "https://images.vexels.com/media/users/3/147102/isolated/preview/082213cb0f9eabb7e6715f59ef7d322a-instagram-profile-icon-by-vexels.png",
    photo: "",
    story: "",
    school: "",
    loading: false,
    error: "",
    createdProduct: false,
    getRedirect: false,
    formData: new FormData(),

    isVerify: false,
  });

  const {
    isVerify,
    defaultImage,
    name,
    profession,
    photo,
    story,
    school,
    loading,
    error,
    createdProduct,
    formData,
  } = values;

  var photoUri = "";

  const preload = () => {
    setValues({
      ...values,
      formData: new FormData(),
    });
  };
  useEffect(() => {
    preload();
  }, []);

  const successMessage = () => {
    return (
      <div
        className=" container alert alert-success m-2"
        style={{ display: createdProduct ? "" : "none" }}
      >
        {createdProduct} Created Successfully.
      </div>
    );
  };
  const errorMessage = () => {
    return (
      <div
        className=" container alert alert-danger m-2"
        style={{ display: error ? "" : "none" }}
      >
        {error}
      </div>
    );
  };
  const loadingMessage = () => {
    return (
      <div
        className=" container alert alert-infor m-2"
        style={{ display: loading ? "" : "none" }}
      >
        Loading...
      </div>
    );
  };

  const onSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, error: "", loading: true });

    var object = {};
    formData.forEach((value, key) => {
      object[key] = value;
    });

    if (isVerify) {
      uploadData(object, (err, msg) => {
        if (err === 1) {
          //setValues({ ...values, });
          setValues({
            ...values,
            error: msg,
            createdProduct: "",
            loading: false,
          });
        } else {
          console.log("Done");

          setValues({
            ...values,
            name: "",
            profession: "",
            photo: "",
            story: "",
            school: "",
            loading: false,
            error: "",
            getRedirect: false,
            formData: new FormData(),
            createdProduct: "Your Story",
          });
        }
      });
    } else {
      setValues({
        ...values,
        error: "CAPTCHA not verified",
        createdProduct: "",
        loading: false,
      });
    }
  };

  const handleChange = (name) => (event) => {
    //const value = name === "photo" ? event[0].base64 : event.target.value;
    const value = event.target.value;

    formData.set(name, value);

    setValues({ ...values, [name]: value });
  };

  //CAPTHAE RESET

  const createProductForm = () => (
    <div className="d-flex justify-content-center">
      <form>
        <div className="form-group ml-5">
          <img
            src={photo === "" ? defaultImage : photo}
            alt="Pic Not Loaded Change Your URL (Optional)"
            width="100"
            height="100"
            onError={() => {
              setValues({ ...values, [name]: photo });
              formData.set("photo", "");
            }}
          />
        </div>

        <div className="form-group">
          <input
            onChange={handleChange("photo")}
            type="string"
            className="form-control"
            placeholder="Your Picture URL (Optional)"
            value={photo}
          />
        </div>
        <div className="form-group">
          <input
            onChange={handleChange("name")}
            type="string"
            className="form-control"
            placeholder="Name"
            required
            value={name}
          />
        </div>
        <div className="form-group">
          <input
            onChange={handleChange("profession")}
            type="string"
            className="form-control"
            placeholder="Profession"
            required
            value={profession}
          />
        </div>

        <div className="form-group">
          <input
            onChange={handleChange("school")}
            type="string"
            className="form-control"
            placeholder="Company/College (Optional)"
            value={school}
          />
        </div>

        <div className="form-group">
          <textarea
            onChange={handleChange("story")}
            name="photo"
            required
            className="form-control"
            placeholder="Your Story"
            value={story}
          />
        </div>

        <ReCAPTCHA
          sitekey="6Le9DLAZAAAAAAx83jpovfZEGJeXTjV1RoGDp05F"
          //ref={(e) => (recaptchaInstance = e)}
          onChange={() => {
            setValues({
              ...values,
              isVerify: true,
            });
          }}
          onloadCallback={() => {
            console.log("Captcha");
          }}
          onExpired={() => {
            setValues({
              ...values,
              isVerify: false,
            });
          }}
        />

        <button
          type="submit"
          onClick={onSubmit}
          className="btn btn-outline-success mb-3"
        >
          Create Story
        </button>
      </form>
    </div>
  );
  return (
    <Base
      title="Add your story"
      className="container p-4"
      linkDesc="Read More Story"
      linkURL="https://werfree.github.io/rejection"
    >
      <div className="row bg-white rounded m-2">
        <div className="col-md-8 offset-md-2 my-4">
          {successMessage()}
          {errorMessage()}
          {loadingMessage()}
          {createProductForm()}
        </div>
      </div>
    </Base>
  );
};
export default App;

