import React from "react";

const Base = ({
  title = "My Title",
  description = "My description",
  linkDesc = "",
  linkURL = "",
  className = "bg-dark text-white mt-5",
  children,
}) => {
  return (
    <div className=" container-fluid body-containers">
      <div className="  bg-dark text-white text-center mt-auto mb-5">
        <h2 className="display-5 text-warning">{title}</h2>
        <p className="lead">{description}</p>
        <a target="" href={linkURL}>
          {linkDesc}
        </a>
      </div>
      <div className={className}>{children}</div>
    </div>
  );
};
export default Base;

